import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { BaseForm } from '@app/components/common/forms/BaseForm/BaseForm';
import { useAppDispatch } from '@app/hooks/reduxHooks';
import { doLogin } from '@app/store/slices/authSlice';
import { notificationController } from '@app/controllers/notificationController';
import * as S from './LoginForm.styles';
import * as Auth from '@app/components/layouts/AuthLayout/AuthLayout.styles';
import { getAllAircrafts, getAllAirports, getAllSchedules } from '@app/store/slices/scheduleSlice';

interface LoginFormData {
  email: string;
  password: string;
}

export const initValues: LoginFormData = {
  email: 'admin@gmail.com',
  password: '123456',
};

export const LoginForm: React.FC = () => {
  const navigate = useNavigate();
  const dispatch = useAppDispatch();

  const [isLoading, setLoading] = useState(false);

  const handleSubmit = (values: LoginFormData) => {
    setLoading(true);
    dispatch(doLogin(values))
      .unwrap()
      .then(() => {
        dispatch(getAllAirports());
        dispatch(getAllAircrafts());
        dispatch(getAllSchedules());
        navigate('/');
      })
      .catch((err) => {
        notificationController.error({ message: err.message });
        setLoading(false);
      });
  };

  return (
    <Auth.FormWrapper>
      <BaseForm layout="vertical" onFinish={handleSubmit} requiredMark="optional" initialValues={initValues}>
        <Auth.FormTitle>Log In</Auth.FormTitle>
        <S.LoginDescription>Enter your email address and password to access admin panel</S.LoginDescription>
        <Auth.FormItem
          name="email"
          label="Email"
          rules={[
            { required: true, message: 'This field is required!' },
            {
              type: 'email',
              message: 'Please input a valid email address!',
            },
          ]}
        >
          <Auth.FormInput placeholder="Email" />
        </Auth.FormItem>
        <Auth.FormItem
          label="Password"
          name="password"
          rules={[{ required: true, message: 'This field is required!' }]}
        >
          <Auth.FormInputPassword placeholder="Password" />
        </Auth.FormItem>
        <BaseForm.Item noStyle>
          <Auth.SubmitButton type="primary" htmlType="submit" loading={isLoading}>
            Log In
          </Auth.SubmitButton>
        </BaseForm.Item>
      </BaseForm>
    </Auth.FormWrapper>
  );
};
