import {
  ADD_APPROVE_LIST,
  ADD_USER,
  APPROVE,
  BOOK,
  CREATE_CUSTOMER,
  CREATE_SCHEDULE,
  FINISH_BOOKING_FOR_CASH,
  INVOKE_PAYMENT,
  UPDATE_PRICE_RULE_BY_DATE,
  UPDATE_PRICE_RULE_BY_SEAT,
} from '@app/graphql/mutation';
import {
  GET_AIRCRAFTS,
  GET_AIRPORTS,
  GET_APPROVELISTS,
  GET_BOOKED_SEATS,
  GET_BOOKLISTS,
  GET_CURRENT_SEAT_PRICE,
  GET_PRICE_RULES_BY_DATES,
  GET_PRICE_RULES_BY_SEATS,
  GET_SCHEDULES,
  GET_SCHEDULES_FOR_TABLE,
  GET_USERS,
} from '@app/graphql/queries';
import { graphqlAPI } from './graphQLApi';

interface PaginationRequest {
  current?: number;
  pageSize?: number;
  total?: number;
}

interface UpdatePriceRuleRequest {
  id: number;
  value: boolean;
}

export interface SeatPrice {
  seat_id: number;
  price: number;
}

export interface CreateScheduleRequest {
  aircraft_id: number;
  arrive_airport_id: number;
  arrive_time: Date;
  depart_airport_id: number;
  depart_time: Date;
  seatsPrice: SeatPrice[];
}

export const getPriceRulesBySeats = (payload: PaginationRequest): Promise<any> =>
  graphqlAPI(GET_PRICE_RULES_BY_SEATS, payload).then(({ data }) => data.listPriceRulesBySeat.data);

export const updatePriceBySeatStatus = (payload: UpdatePriceRuleRequest): Promise<any> =>
  graphqlAPI(UPDATE_PRICE_RULE_BY_SEAT, payload).then(({ data }) => data.updatePriceRuleBySeat);

export const getPriceRulesByDates = (payload: PaginationRequest): Promise<any> =>
  graphqlAPI(GET_PRICE_RULES_BY_DATES, payload).then(({ data }) => {
    console.log(data);
    return data.listPriceRulesByDate.data;
  });

export const updatePriceByDateStatus = (payload: UpdatePriceRuleRequest): Promise<any> =>
  graphqlAPI(UPDATE_PRICE_RULE_BY_DATE, payload).then(({ data }) => data.updatePriceRuleByDate);

export const getAirports = (payload: PaginationRequest): Promise<any> =>
  graphqlAPI(GET_AIRPORTS, payload).then(({ data }) => data.airports.data);

export const getAircrafts = (): Promise<any> => graphqlAPI(GET_AIRCRAFTS, {}).then(({ data }) => data.aircrafts.data);

export const getSchedules = (): Promise<any> =>
  graphqlAPI(GET_SCHEDULES, {}).then(({ data }) => data.schedules.data.schedules);

export const createSchedule = (payload: CreateScheduleRequest): Promise<any> =>
  graphqlAPI(CREATE_SCHEDULE, payload).then(({ data }) => data.createSchedule);

export const createCustomer = (payload: any): Promise<any> =>
  graphqlAPI(CREATE_CUSTOMER, payload).then(({ data }) => data.createCustomer.data);

export const addBook = (payload: any): Promise<any> => graphqlAPI(BOOK, payload).then(({ data }) => data.book);

export const addApprovelist = (payload: any): Promise<any> =>
  graphqlAPI(ADD_APPROVE_LIST, payload).then(({ data }) => data.addApproveList);

export const getCurrentSeatPrice = (payload: any): Promise<any> =>
  graphqlAPI(GET_CURRENT_SEAT_PRICE, payload).then(({ data }) => data.currentSeatPrice.data);

export const getBookedSeats = (payload: any): Promise<any> =>
  graphqlAPI(GET_BOOKED_SEATS, payload).then(({ data }) => data.getBookedSeats.data);

export const getSchedulesForTable = (payload: any): Promise<any> =>
  graphqlAPI(GET_SCHEDULES_FOR_TABLE, payload).then(({ data }) => data.schedules.data);

export const getBooklists = (payload: any): Promise<any> =>
  graphqlAPI(GET_BOOKLISTS, payload).then(({ data }) => data.booklists.data);

export const getApprovelists = (payload: any): Promise<any> =>
  graphqlAPI(GET_APPROVELISTS, payload).then(({ data }) => data.approvelists.data);

export const invokePayment = (payload: any): Promise<any> =>
  graphqlAPI(INVOKE_PAYMENT, payload).then(({ data }) => data.invokePayment);

export const approve = (payload: any): Promise<any> => graphqlAPI(APPROVE, payload).then(({ data }) => data.approve);

export const finishBookingForCash = (payload: any): Promise<any> =>
  graphqlAPI(FINISH_BOOKING_FOR_CASH, payload).then(({ data }) => data.finishBookingForCash);

export const getUsersForTable = (payload: any): Promise<any> =>
  graphqlAPI(GET_USERS, payload).then(({ data }) => data.users.data);

export const addUser = (payload: any): Promise<any> => graphqlAPI(ADD_USER, payload).then(({ data }) => data.addUser);
