import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { AirportModel } from '@app/domain/AirportModel';
import { getAircrafts, getAirports, getSchedules } from '@app/api/api';
import { AircraftModel } from '@app/domain/AircraftModel';
import { ScheduleModel } from '@app/domain/ScheduleModel';

export interface BaseDataState {
  airports: AirportModel[];
  aircrafts: AircraftModel[];
  schedules: ScheduleModel[];
}

const initialState: BaseDataState = {
  airports: [],
  aircrafts: [],
  schedules: [],
};

export const getAllAirports = createAsyncThunk('schedule/getAllAirports', async () =>
  getAirports({}).then((res) => {
    return res.airports;
  }),
);

export const getAllAircrafts = createAsyncThunk('schedule/getAllAircrafts', async () =>
  getAircrafts().then((res) => {
    return res;
  }),
);

export const getAllSchedules = createAsyncThunk('schedule/getAllSchedules', async () =>
  getSchedules().then((res) => {
    console.log('get all schedules', res);
    return res;
  }),
);

export const scheduleSlice = createSlice({
  name: 'schedule',
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder.addCase(getAllAirports.fulfilled, (state, action) => {
      state.airports = action.payload;
    });
    builder.addCase(getAllAircrafts.fulfilled, (state, action) => {
      state.aircrafts = action.payload;
    });
    builder.addCase(getAllSchedules.fulfilled, (state, action) => {
      state.schedules = action.payload;
    });
  },
});

export default scheduleSlice.reducer;
