import { BaseForm } from '@app/components/common/forms/BaseForm/BaseForm';
import styled from 'styled-components';
import { DatePicker } from '@app/components/common/pickers/DatePicker';
import { Select } from '@app/components/common/selects/Select/Select';
import React, { useState } from 'react';
import { DayjsDatePicker } from '@app/components/common/pickers/DayjsDatePicker';
import { useAppDispatch, useAppSelector } from '@app/hooks/reduxHooks';
import { SeatModel } from '@app/domain/AircraftModel';
import { Col, InputNumber, Row } from 'antd';
import { Card } from '@app/components/common/Card/Card';
import { mergeBy } from '@app/utils/utils';
import { PageTitle } from '@app/components/common/PageTitle/PageTitle';
import { Button } from '@app/components/common/buttons/Button/Button';
import { createSchedule, CreateScheduleRequest, SeatPrice } from '@app/api/api';
import { notificationController } from '@app/controllers/notificationController';
import { getAllSchedules } from '@app/store/slices/scheduleSlice';
import { useNavigate } from 'react-router-dom';

const ButtonWrapper = styled.div`
  margin-top: 1.25rem;
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: end;
`;

interface FieldData {
  name: string | number;
  value?: any;
}

export const ScheduleAddPage: React.FC = () => {
  const [isLoading, setLoading] = useState(false);
  const [form] = BaseForm.useForm();
  const [fields, setFields] = useState<FieldData[]>([]);
  const dispatch = useAppDispatch();
  const navigate = useNavigate();

  const aircrafts = useAppSelector((state) => state.baseData.aircrafts);
  const airports = useAppSelector((state) => state.baseData.airports);

  const airportsSelectData = airports.map((airport) => {
    return { label: airport.name, value: airport.id };
  });

  const aircraftsSelectData = aircrafts.map((aircraft) => {
    return { label: aircraft.name, value: aircraft.id };
  });

  const dateFields = ['depart_date', 'arrive_date'];
  const timeFields = ['depart_time', 'arrive_time'];

  const [seatTypes, setSeatTypes] = useState<SeatModel[]>([]);

  const aircraftChange = (value: number) => {
    setSeatTypes(aircrafts[value - 1].seats);
  };

  const onFinish = async () => {
    setLoading(true);

    let json: any = {};
    fields.forEach((field) => {
      const key = field.name;
      const value =
        dateFields.indexOf(String(field.name)) != -1 && field.value
          ? field.value.format('YYYY-MM-DD')
          : timeFields.indexOf(String(field.name)) != -1 && field.value
          ? field.value.format('HH:mm:ss')
          : field.value;
      json = {
        ...json,
        ...{
          [key]: value,
        },
      };
    });
    json.depart_time = json.depart_date + ' ' + json.depart_time;
    json.arrive_time = json.arrive_date + ' ' + json.arrive_time;

    const seats: unknown[] = [];
    Object.entries(json).forEach(([key, value]) => {
      const _split = key.split('_');
      if (_split[0] === 'seatId') seats.push({ seat_id: Number(_split[1]), price: value });
    });

    const data: CreateScheduleRequest = {
      aircraft_id: json.aircraft_id,
      depart_airport_id: json.depart_airport_id,
      arrive_airport_id: json.arrive_airport_id,
      depart_time: json.depart_time,
      arrive_time: json.arrive_time,
      seatsPrice: seats as SeatPrice[],
    };

    await createSchedule(data).then((res) => {
      if (res.isSuccess) {
        notificationController.success({ message: 'New Schedule created successfully' });
        dispatch(getAllSchedules());
      } else {
        notificationController.error({ message: res.message });
      }
    });
    setLoading(false);

    navigate('/schedule_manage/schedule');
  };

  return (
    <>
      <PageTitle>Add New Schedule</PageTitle>
      <Card id="add-schedule" title="Add New Schedule" padding="1.25rem 3rem 3rem">
        <BaseForm
          name="addScheduleForm"
          form={form}
          onFinish={onFinish}
          onFieldsChange={(_, allFields) => {
            const currentFields = allFields.map((item) => {
              if (Array.isArray(item.name) && item.name[0] === 'aircraft_id' && item.touched)
                aircraftChange(item.value);

              return {
                name: Array.isArray(item.name) ? item.name[0] : '',
                value: item.value,
              };
            });
            const uniqueData = mergeBy(fields, currentFields, 'name');
            setFields(uniqueData);
          }}
        >
          <Row gutter={[30, 30]}>
            <Col span={8}>
              <BaseForm.Item
                name="aircraft_id"
                label="Aircraft"
                rules={[{ required: true, message: 'Aircraft required' }]}
              >
                <Select options={aircraftsSelectData} placeholder="Select aircraft" />
              </BaseForm.Item>
            </Col>
            <Col span={2} />
            {seatTypes.map((seatType) => (
              <Col span={6} key={seatType.id}>
                <BaseForm.Item
                  label={`${seatType.seat_type} Price`}
                  name={`seatId_${seatType.id}_price`}
                  fieldKey={[seatType.id, 'price']}
                  rules={[{ required: true, message: `${seatType.seat_type} Price Required` }]}
                >
                  <InputNumber min={1} style={{ width: '100%' }} prefix="$" />
                </BaseForm.Item>
              </Col>
            ))}
          </Row>
          <Row gutter={[30, 30]}>
            <Col span={8}>
              <BaseForm.Item
                name="depart_airport_id"
                label="Depart Airport"
                rules={[{ required: true, message: 'Depart Airport required' }]}
              >
                <Select options={airportsSelectData} placeholder="Select depart airport" />
              </BaseForm.Item>
            </Col>
            <Col span={2} />
            <Col span={6}>
              <BaseForm.Item
                name="depart_date"
                label="Depart Date"
                rules={[{ required: true, message: 'Depart Date required' }]}
              >
                <DatePicker format="YYYY-MM-DD" style={{ width: '100%' }} />
              </BaseForm.Item>
            </Col>
            <Col span={6}>
              <BaseForm.Item
                name="depart_time"
                label="Depart Time"
                rules={[{ required: true, message: 'Depart Time required' }]}
              >
                <DayjsDatePicker.TimePicker style={{ width: '100%' }} />
              </BaseForm.Item>
            </Col>
          </Row>
          <Row gutter={[30, 30]}>
            <Col span={8}>
              <BaseForm.Item
                name="arrive_airport_id"
                label="Arrive Airport"
                rules={[{ required: true, message: 'Arrive Airport required' }]}
              >
                <Select options={airportsSelectData} placeholder="Select depart airport" />
              </BaseForm.Item>
            </Col>
            <Col span={2} />
            <Col span={6}>
              <BaseForm.Item
                name="arrive_date"
                label="Arrive Date"
                rules={[{ required: true, message: 'Arrive Date required' }]}
              >
                <DatePicker format="YYYY-MM-DD" style={{ width: '100%' }} />
              </BaseForm.Item>
            </Col>
            <Col span={6}>
              <BaseForm.Item
                name="arrive_time"
                label="Arrive Time"
                rules={[{ required: true, message: 'Arrive Time required' }]}
              >
                <DayjsDatePicker.TimePicker style={{ width: '100%' }} />
              </BaseForm.Item>
            </Col>
          </Row>
          <BaseForm.Item noStyle>
            <ButtonWrapper>
              <Button type="primary" loading={isLoading} htmlType="submit">
                Add Schedule
              </Button>
            </ButtonWrapper>
          </BaseForm.Item>
        </BaseForm>
      </Card>
    </>
  );
};
