import { UserModel } from '@app/domain/UserModel';
import { graphqlAPI } from '@app/api/graphQLApi';
import { LOGIN } from '@app/graphql/queries';
export interface AuthData {
  email: string;
  password: string;
}

export interface LoginRequest {
  email: string;
  password: string;
}

export interface LoginResponse {
  token: string;
  user: UserModel;
}

export const login = (loginPayload: LoginRequest): Promise<any> =>
  graphqlAPI(LOGIN, loginPayload).then(({ data }) => data);
