import SeatPicker from '@app/components/SeatPicker';
import React, { useEffect, useState } from 'react';

interface SeatMapProps {
  entireSeats: string[];
  reservedSeats: string[];
  selectedSeats: string[];
  onAddSeat: (seat_number: string) => void;
  onRemoveSeat: (seat_number: string) => void;
}

export const Seatmap: React.FC<SeatMapProps> = ({
  entireSeats,
  reservedSeats,
  selectedSeats,
  onAddSeat,
  onRemoveSeat,
}) => {
  const [loading, setLoading] = useState(false);
  const [seatRows, setSeatRows] = useState<any[][]>([]);
  const [firstRowIndex, setFirstRowIndex] = useState(1);

  useEffect(() => {
    const asyncFunc = async () => {
      if (entireSeats.length === 0) {
        setSeatRows([]);
        setLoading(true);
        await new Promise((resolve) => setTimeout(resolve, 100));
        setLoading(false);
        return;
      }

      const firstRowIndex = Math.min(
        ...entireSeats.map((seat) => {
          return Number(seat.slice(0, seat.length - 1));
        }),
      );
      setFirstRowIndex(firstRowIndex);

      const seatRows = getRows(entireSeats, firstRowIndex, selectedSeats, reservedSeats);
      setSeatRows(seatRows);
      console.log(seatRows);

      setLoading(true);
      await new Promise((resolve) => setTimeout(resolve, 100));
      setLoading(false);
    };

    asyncFunc();
  }, [entireSeats, selectedSeats, reservedSeats]);

  const addSeatCallback = async ({ row, number, id }: any, addCb: any) => {
    setLoading(true);
    await new Promise((resolve) => setTimeout(resolve, 300));
    console.log(`Added seat ${number}, row ${row}, id ${id}`);
    const newTooltip = `tooltip for id-${id} added by callback`;
    addCb(row, number, id, newTooltip);
    onAddSeat(`${row}${number}`);
    setLoading(false);
  };

  const removeSeatCallback = async ({ row, number, id }: any, removeCb: any) => {
    setLoading(true);
    await new Promise((resolve) => setTimeout(resolve, 300));
    console.log(`"Removed seat ${number}, row ${row}, id ${id}"`);
    const newTooltip = ['A', 'B', 'C'].includes(row) ? null : '';
    removeCb(row, number, newTooltip);
    onRemoveSeat(`${row}${number}`);
    setLoading(false);
  };

  const getRows = (entireSeats: string[], firstRowIndex: number, selectedSeats: string[], reservedSeats: string[]) => {
    let row: any[] = [];
    let rowIndex = firstRowIndex;
    let rows: any[][] = [];

    entireSeats.forEach((seat) => {
      if (seat.slice(0, seat.length - 1) === String(rowIndex + 1)) {
        rows.push(row);
        row = [];
        ++rowIndex;
      }

      if (seat.slice(0, seat.length - 1) === String(rowIndex)) row.push(seat);
    });

    rows.push(row);

    rows = rows.map((row) => {
      const tmp_1 = 4 - row.filter((item) => item.slice(-1) >= 'A' && item.slice(-1) <= 'D').length;
      const tmp_2 = 4 - row.filter((item) => item.slice(-1) >= 'F' && item.slice(-1) <= 'J').length;

      let index_1 = -1;
      let index_2 = -1;

      if (row[row.length - 1].slice(-1) <= 'G') index_2 = row.length;
      else {
        for (let i = row.length - 1; i >= 1; --i) {
          if (row[i].slice(-1) >= 'H' && row[i - 1].slice(-1) <= 'G') index_2 = i;
        }
      }

      if (row[0].slice(-1) >= 'C') index_1 = 0;
      else {
        for (let i = row.length - 1; i >= 1; --i) {
          if (row[i].slice(-1) >= 'C' && row[i - 1].slice(-1) <= 'B') index_1 = i;
        }
      }

      row.splice(index_2, 0, Array(tmp_2 + 1).fill('empty'));
      row.splice(index_1, 0, Array(tmp_1 + 1).fill('empty'));

      const tmp = row.join();
      row = tmp.split(',');

      return row;
    });

    let index = 1;
    const seatRows: any[][] = [];
    let seatRow: any[] = [];

    rows.map((row) => {
      row.map((item) => {
        if (item === 'empty') seatRow.push(null);
        else {
          let rowItem = { id: index, number: item.slice(-1) };
          if (reservedSeats.includes(item)) rowItem = { ...rowItem, ...{ isReserved: true } };
          if (selectedSeats.includes(item)) rowItem = { ...rowItem, ...{ isSelected: true } };

          seatRow.push(rowItem);
          ++index;
        }
      });
      seatRows.push(seatRow);
      seatRow = [];
    });

    console.log(seatRows);
    return seatRows;
  };

  return (
    <SeatPicker
      addSeatCallback={addSeatCallback}
      removeSeatCallback={removeSeatCallback}
      rows={seatRows}
      firstRowIndex={firstRowIndex}
      maxReservableSeats={10}
      visible
      selectedByDefault={true}
      loading={loading}
      tooltipProps={{ multiline: true }}
    />
  );
};
