import React from 'react';
import { Avatar } from 'antd';
import { UserModel } from '@app/domain/UserModel';
import * as S from './ProfileInfo.styles';

import StubAvatar from '../../../../assets/images/stub-avatar.webp';

interface ProfileInfoProps {
  profileData: UserModel | null;
}

export const ProfileInfo: React.FC<ProfileInfoProps> = ({ profileData }) => {
  return profileData ? (
    <S.Wrapper>
      <S.ImgWrapper>
        <Avatar shape="circle" src={profileData?.img_url ?? StubAvatar} alt="Profile" />
      </S.ImgWrapper>
      <S.Title>{`${profileData?.first_name} ${profileData?.last_name}`}</S.Title>
    </S.Wrapper>
  ) : null;
};
