import React, { useState, useCallback, useEffect } from 'react';
import { PageTitle } from '@app/components/common/PageTitle/PageTitle';
import styled from 'styled-components';
import { Card as CommonCard } from '@app/components/common/Card/Card';
import { useMounted } from '@app/hooks/useMounted';
import { ColumnsType } from 'antd/es/table';
import { TablePaginationConfig } from 'antd';
import { Table } from '@app/components/common/Table/Table';
import { getBooklists } from '@app/api/api';
import { toYYYYMMDD_HHMMSS } from '@app/utils/utils';
import { useNavigate } from 'react-router-dom';
import { useAppSelector } from '@app/hooks/reduxHooks';

const TablesWrapper = styled.div`
  margin-top: 1.875rem;
`;

const Card = styled(CommonCard)`
  margin-bottom: 2rem;
`;

interface Pagination {
  current?: number;
  pageSize?: number;
  total?: number;
}

interface TableRow {
  id: number;
  code: string;
  airline: string;
  trip_type: string;
  customer: string;
  // customer_email: string;
  // customer_phone: string;
  outgo_time: Date;
  return_time: Date;
  total_cost: number;
  agent_name: string;
  payment_type: string;
  payment_status: string;
  status: string;
  createdAt: Date;
}

const BooklistPage: React.FC = () => {
  return (
    <>
      <PageTitle>Book list</PageTitle>
      <TablesWrapper>
        <Card id="book-table" title="Books" padding="1.25rem 1.25rem 0">
          <BooklistTable />
        </Card>
      </TablesWrapper>
    </>
  );
};

const initialPagination: Pagination = {
  current: 1,
  pageSize: 5,
};

export const BooklistTable: React.FC = () => {
  const [tableData, setTableData] = useState<{ data: TableRow[]; pagination: Pagination; loading: boolean }>({
    data: [],
    pagination: initialPagination,
    loading: false,
  });
  const { isMounted } = useMounted();

  const { role, id } = useAppSelector((state) => state.user.user!);

  const fetch = useCallback(
    (pagination: Pagination) => {
      setTableData((tableData) => ({ ...tableData, loading: true }));
      getBooklists({ pagination, role, user_id: id }).then((res) => {
        console.log('getBooklists', res);
        if (isMounted.current) {
          const booklists = res.booklists;
          const data = booklists.map((booklist: any) => {
            return {
              id: booklist.id,
              code: booklist.code,
              airline: `${booklist.outgo_schedule.depart_airport.name}-${booklist.outgo_schedule.arrive_airport.name}`,
              trip_type: booklist.return_schedule ? 'Round Trip' : 'One Way',
              customer: `${booklist.customer.first_name} ${booklist.customer.last_name}\n${booklist.customer.email}\n${
                booklist.customer.phone_numer ?? ''
              }`,
              outgo_time: toYYYYMMDD_HHMMSS(booklist.outgo_schedule.depart_time ?? ''),
              return_time: toYYYYMMDD_HHMMSS(!booklist.return_schedule ? '' : booklist.return_schedule.depart_time),
              total_cost: booklist.total_cost,
              agent_name: `${booklist.agent.first_name} ${booklist.agent.last_name}`,
              payment_type: booklist.payment_type,
              payment_status: booklist.payment_status,
              status: booklist.status,
            };
          });

          setTableData({ data: data, pagination: res.pagination, loading: false });
        }
      });
    },
    [isMounted],
  );

  useEffect(() => {
    fetch(initialPagination);
  }, [fetch]);

  const handleTableChange = (pagination: TablePaginationConfig) => {
    fetch(pagination);
  };

  const columns: ColumnsType<TableRow> = [
    {
      title: 'Code',
      dataIndex: 'code',
    },
    {
      title: 'Airline',
      dataIndex: 'airline',
    },
    {
      title: 'Trip Type',
      dataIndex: 'trip_type',
    },
    {
      title: 'Customer',
      dataIndex: 'customer',
    },
    {
      title: 'Total Cost',
      dataIndex: 'total_cost',
    },
    {
      title: 'Payment',
      dataIndex: 'payment_type',
    },
    {
      title: 'Payment\nStatus',
      dataIndex: 'payment_status',
    },
    {
      title: 'Booking\nStatus',
      dataIndex: 'status',
    },
  ];

  if (role === 'ADMIN') columns.splice(5, 0, { title: 'Agent', dataIndex: 'agent_name' });

  return (
    <Table
      columns={columns}
      dataSource={tableData.data}
      pagination={tableData.pagination}
      loading={tableData.loading}
      onChange={handleTableChange}
      scroll={{ x: 800 }}
      bordered
      style={{ whiteSpace: 'pre' }}
    />
  );
};

export default BooklistPage;
