import React, { useState, useCallback, useEffect } from 'react';
import { PageTitle } from '@app/components/common/PageTitle/PageTitle';
import styled from 'styled-components';
import { Card as CommonCard } from '@app/components/common/Card/Card';
import { useMounted } from '@app/hooks/useMounted';
import { ColumnsType } from 'antd/es/table';
import { TablePaginationConfig } from 'antd';
import { Table } from '@app/components/common/Table/Table';
import { getSchedulesForTable } from '@app/api/api';
import { toYYYYMMDD_HHMMSS } from '@app/utils/utils';
import { Button } from '@app/components/common/buttons/Button/Button';
import { useNavigate } from 'react-router-dom';

const TablesWrapper = styled.div`
  margin-top: 1.875rem;
`;

const Card = styled(CommonCard)`
  margin-bottom: 2rem;
`;

interface Pagination {
  current?: number;
  pageSize?: number;
  total?: number;
}

interface TableRow {
  id: number;
  depart_airport: string;
  arrive_airport: string;
  aircraft: string;
  depart_time: Date;
  arrive_time: Date;
}

const ButtonWrapper = styled.div`
  margin-bottom: 1.25rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: end;
`;

const SchedulePage: React.FC = () => {
  const navigate = useNavigate();

  return (
    <>
      <PageTitle>Schedules</PageTitle>
      <TablesWrapper>
        <Card id="schedule-table" title="Schedules" padding="1.25rem 1.25rem 0">
          <ButtonWrapper>
            <Button severity="info" onClick={() => navigate('/schedule_manage/schedule/add')} loading={false}>
              Add New Schedule
            </Button>
          </ButtonWrapper>
          <ScheduleTable />
        </Card>
      </TablesWrapper>
    </>
  );
};

const initialPagination: Pagination = {
  current: 1,
  pageSize: 5,
};

export const ScheduleTable: React.FC = () => {
  const [tableData, setTableData] = useState<{ data: TableRow[]; pagination: Pagination; loading: boolean }>({
    data: [],
    pagination: initialPagination,
    loading: false,
  });
  const { isMounted } = useMounted();

  const fetch = useCallback(
    (pagination: Pagination) => {
      setTableData((tableData) => ({ ...tableData, loading: true }));
      getSchedulesForTable(pagination).then((res) => {
        console.log('getSchedulesForTable', res);
        if (isMounted.current) {
          const schedules = res.schedules;
          const data = schedules.map((schedule: any) => {
            return {
              id: schedule.id,
              depart_airport: schedule.depart_airport.name,
              arrive_airport: schedule.arrive_airport.name,
              aircraft: schedule.aircraft.name,
              depart_time: toYYYYMMDD_HHMMSS(schedule.depart_time),
              arrive_time: toYYYYMMDD_HHMMSS(schedule.arrive_time),
            };
          });

          setTableData({ data: data, pagination: res.pagination, loading: false });
        }
      });
    },
    [isMounted],
  );

  useEffect(() => {
    fetch(initialPagination);
  }, [fetch]);

  const handleTableChange = (pagination: TablePaginationConfig) => {
    fetch(pagination);
  };

  const columns: ColumnsType<TableRow> = [
    {
      title: 'Depart Airport',
      dataIndex: 'depart_airport',
    },
    {
      title: 'Arrive Airport',
      dataIndex: 'arrive_airport',
    },
    {
      title: 'Aircraft',
      dataIndex: 'aircraft',
    },
    {
      title: 'Depart Time',
      dataIndex: 'depart_time',
    },
    {
      title: 'Arrive Time',
      dataIndex: 'arrive_time',
    },
  ];

  return (
    <Table
      columns={columns}
      dataSource={tableData.data}
      pagination={tableData.pagination}
      loading={tableData.loading}
      onChange={handleTableChange}
      scroll={{ x: 800 }}
      bordered
    />
  );
};

export default SchedulePage;
