import React, { useState, useCallback, useEffect } from 'react';
import { PageTitle } from '@app/components/common/PageTitle/PageTitle';
import styled from 'styled-components';
import { Card as CommonCard } from '@app/components/common/Card/Card';
import { useMounted } from '@app/hooks/useMounted';
import { ColumnsType } from 'antd/es/table';
import { TablePaginationConfig, Space } from 'antd';
import { Table } from '@app/components/common/Table/Table';
import { approve, finishBookingForCash, getApprovelists } from '@app/api/api';
import { toYYYYMMDD_HHMMSS } from '@app/utils/utils';
import { Button } from '@app/components/common/buttons/Button/Button';
import { useNavigate } from 'react-router-dom';
import { useAppSelector } from '@app/hooks/reduxHooks';
import { notificationController } from '@app/controllers/notificationController';

const TablesWrapper = styled.div`
  margin-top: 1.875rem;
`;

const Card = styled(CommonCard)`
  margin-bottom: 2rem;
`;

interface Pagination {
  current?: number;
  pageSize?: number;
  total?: number;
}

interface TableRow {
  id: number;
  airline: string;
  trip_type: string;
  customer: string;
  outgo_time: Date;
  return_time: Date;
  total_cost: number;
  agent_name: string;
  status: string;
  createdAt: Date;
}

const ApprovelistPage: React.FC = () => {
  return (
    <>
      <PageTitle>Approvement list</PageTitle>
      <TablesWrapper>
        <Card id="approve-table" title="For Approvement" padding="1.25rem 1.25rem 0">
          <ApprovelistTable />
        </Card>
      </TablesWrapper>
    </>
  );
};

const initialPagination: Pagination = {
  current: 1,
  pageSize: 5,
};

export const ApprovelistTable: React.FC = () => {
  const [tableData, setTableData] = useState<{ data: TableRow[]; pagination: Pagination; loading: boolean }>({
    data: [],
    pagination: initialPagination,
    loading: false,
  });
  const [actionBtnLabel, setActionBtnLabel] = useState('');
  const [currentPagination, setCurrentPagination] = useState(initialPagination);
  const { isMounted } = useMounted();
  const { role, id } = useAppSelector((state) => state.user.user!);

  useEffect(() => {
    if (role === 'AGENT') setActionBtnLabel('Book');
    else if (role === 'ADMIN') setActionBtnLabel('Approve');
  }, [role]);

  const fetch = useCallback(
    (pagination: Pagination) => {
      setTableData((tableData) => ({ ...tableData, loading: true }));
      getApprovelists({ pagination, role, user_id: id }).then((res) => {
        console.log('getApprovelists', res);
        if (isMounted.current) {
          const approvelists = res.approvelists;
          const data = approvelists.map((approvelist: any) => {
            return {
              id: approvelist.id,
              code: approvelist.code,
              airline: `${approvelist.outgo_schedule.depart_airport.name}-${approvelist.outgo_schedule.arrive_airport.name}`,
              trip_type: approvelist.return_schedule ? 'Round Trip' : 'One Way',
              customer: `${approvelist.customer.first_name} ${approvelist.customer.last_name}\n${
                approvelist.customer.email
              }\n${approvelist.customer.phone_numer ?? ''}`,
              outgo_time: toYYYYMMDD_HHMMSS(approvelist.outgo_schedule.depart_time ?? ''),
              return_time: toYYYYMMDD_HHMMSS(
                !approvelist.return_schedule ? '' : approvelist.return_schedule.depart_time,
              ),
              total_cost: approvelist.total_cost,
              agent_name: `${approvelist.agent.first_name} ${approvelist.agent.last_name}`,
              payment_type: approvelist.payment_type,
              payment_status: approvelist.payment_status,
              status: approvelist.status,
            };
          });

          setTableData({ data: data, pagination: res.pagination, loading: false });
        }
      });
    },
    [isMounted],
  );

  useEffect(() => {
    fetch(initialPagination);
  }, [fetch]);

  const handleTableChange = (pagination: TablePaginationConfig) => {
    fetch(pagination);
    setCurrentPagination(pagination);
  };

  const handleAction = async (id: number, event: React.MouseEvent<HTMLDivElement>) => {
    event.preventDefault();
    event.stopPropagation();

    if (role === 'ADMIN') {
      await approve({ id }).then((res) => {
        if (res.isSuccess) {
          notificationController.success({ message: 'Approved successfully' });
          fetch(currentPagination);
        } else {
          notificationController.error({ message: res.message });
        }
      });
    } else if (role === 'AGENT') {
      await finishBookingForCash({ approve_id: id }).then((res) => {
        if (res.isSuccess) {
          notificationController.success({ message: 'Booked successfully' });
          fetch(currentPagination);
        } else {
          notificationController.error({ message: res.message });
        }
      });
    }
  };

  const columns: ColumnsType<TableRow> = [
    {
      title: 'Airline',
      dataIndex: 'airline',
    },
    {
      title: 'Trip Type',
      dataIndex: 'trip_type',
    },
    {
      title: 'Customer',
      dataIndex: 'customer',
    },
    {
      title: 'Total Cost',
      dataIndex: 'total_cost',
    },
    {
      title: 'Approvement Status',
      dataIndex: 'status',
    },
  ];

  if (role === 'ADMIN') columns.push({ title: 'Agent', dataIndex: 'agent_name' });

  const actionColumn = {
    title: 'Action',
    dataIndex: 'action',
    render: (text: string, record: { id: number; status: string }) => {
      return (
        <Space>
          <Button
            disabled={
              (role === 'AGENT' && record.status === 'PENDING') || (role === 'ADMIN' && record.status === 'APPROVED')
            }
            type="ghost"
            onClick={(event: React.MouseEvent<HTMLDivElement>) => handleAction(record.id, event)}
          >
            {actionBtnLabel}
          </Button>
        </Space>
      );
    },
  };

  columns.push(actionColumn);

  return (
    <Table
      columns={columns}
      dataSource={tableData.data}
      pagination={tableData.pagination}
      loading={tableData.loading}
      onChange={handleTableChange}
      scroll={{ x: 800 }}
      bordered
      style={{ whiteSpace: 'pre' }}
    />
  );
};

export default ApprovelistPage;
