import { addUser } from '@app/api/api';
import { Button } from '@app/components/common/buttons/Button/Button';
import { Card as CommonCard } from '@app/components/common/Card/Card';
import { BaseForm } from '@app/components/common/forms/BaseForm/BaseForm';
import { Input } from '@app/components/common/inputs/Input/Input';
import { DatePicker } from '@app/components/common/pickers/DatePicker';
import { Select, Option } from '@app/components/common/selects/Select/Select';
import { mergeBy } from '@app/utils/utils';
import { Col, Row } from 'antd';
import React, { useState } from 'react';
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';
import { notificationController } from '@app/controllers/notificationController';
import { InputPassword } from '@app/components/common/inputs/InputPassword/InputPassword';

const ButtonWrapper = styled.div`
  margin-top: 1.25rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: end;
`;

const PhoneItem = styled(BaseForm.Item)`
  .ant-input-group-addon {
    padding: 0;
  }

  .ant-input-group-addon > div {
    width: 100% !important;
  }
`;

export const PhoneSelect = styled(Select)`
  width: 5.9375rem;
`;

const Card = styled(CommonCard)`
  width: 100%;
  margin-bottom: 1.25rem;
  .ant-card-body:before {
    display: none;
  }
  .ant-card-body:after {
    display: none;
  }
  &.ant-card-bordered {
    border: 1px solid var(--border-color);
  }
`;

interface FieldData {
  name: string | number;
  value?: any;
}

export const UserAddPage: React.FC = () => {
  const [form] = BaseForm.useForm();
  const [fields, setFields] = useState<FieldData[]>([]);
  const [isLoading, setLoading] = useState(false);

  const navigate = useNavigate();

  const prefixSelector = (
    <BaseForm.Item name="prefix" noStyle>
      <PhoneSelect>
        <Option value="233">+233</Option>
      </PhoneSelect>
    </BaseForm.Item>
  );

  const roleSelectData = [
    {
      label: 'Admin',
      value: 'ADMIN',
    },
    {
      label: 'Agent',
      value: 'AGENT',
    },
    {
      label: 'User',
      value: 'USER',
    },
  ];

  const onFinish = async () => {
    setLoading(true);
    let json: any = {};

    fields.forEach((field) => {
      const key = field.name;
      const value = String(field.name) === 'birthday' && field.value ? field.value.format('YYYY-MM-DD') : field.value;
      json = {
        ...json,
        ...{
          [key]: value,
        },
      };
    });

    const userData = {
      first_name: json.first_name,
      last_name: json.last_name,
      email: json.email,
      phone_number: json.phone_number,
      birthday: json.birthday,
      password: json.password,
      role: json.role,
    };

    console.log(userData);

    await addUser(userData).then((res) => {
      if (res.isSuccess) {
        notificationController.success({ message: 'Added User successfully' });
      } else {
        notificationController.error({ message: res.message });
      }
    });

    setLoading(false);
    navigate('/user_manage/user');
  };

  return (
    <BaseForm
      name="addUser"
      form={form}
      onFinish={onFinish}
      onFieldsChange={(_, allFields) => {
        const currentFields = allFields.map((item) => {
          return {
            name: Array.isArray(item.name) ? item.name[0] : '',
            value: item.value,
          };
        });
        const uniqueData = mergeBy(fields, currentFields, 'name');
        setFields(uniqueData);
      }}
    >
      <Card id="user-form" title="User Information" padding="1.25rem 5rem 1.25rem 5rem">
        <Row gutter={[30, 30]}>
          <Col span={12}>
            <BaseForm.Item
              name="first_name"
              label="First Name"
              rules={[{ required: true, message: 'First Name required' }]}
            >
              <Input />
            </BaseForm.Item>
          </Col>
          <Col span={12}>
            <BaseForm.Item
              name="last_name"
              label="Last Name"
              rules={[{ required: true, message: 'Last Name required' }]}
            >
              <Input />
            </BaseForm.Item>
          </Col>
        </Row>
        <Row gutter={[30, 30]}>
          <Col span={24}>
            <BaseForm.Item name="birthday" label="Birthday" rules={[{ required: true, message: 'Birthday required' }]}>
              <DatePicker format="YYYY-MM-DD" />
            </BaseForm.Item>
          </Col>
        </Row>
        <Row gutter={[30, 30]}>
          <Col span={12}>
            <PhoneItem name="phone_number" label="Phone" rules={[{ required: true, message: 'Phone number required' }]}>
              <Input addonBefore={prefixSelector} />
            </PhoneItem>
          </Col>
          <Col span={12}>
            <BaseForm.Item
              name="email"
              label="Email"
              rules={[
                {
                  type: 'email',
                  required: true,
                  message: 'Email required',
                },
              ]}
            >
              <Input />
            </BaseForm.Item>
          </Col>
        </Row>
        <Row gutter={[30, 30]}>
          <Col span={12}>
            <BaseForm.Item
              name="password"
              label="Password"
              rules={[{ required: true, message: 'Password is required' }]}
            >
              <InputPassword />
            </BaseForm.Item>
          </Col>
          <Col span={12}>
            <BaseForm.Item
              name="confirmPassword"
              label="Confirm Password"
              dependencies={['password']}
              rules={[
                { required: true, message: 'The two passwords that you entered do not match!' },
                ({ getFieldValue }) => ({
                  validator(_, value) {
                    if (!value || getFieldValue('password') === value) {
                      return Promise.resolve();
                    }
                    return Promise.reject(new Error('The two passwords that you entered do not match!'));
                  },
                }),
              ]}
            >
              <InputPassword />
            </BaseForm.Item>
          </Col>
        </Row>
        <Row gutter={[30, 30]}>
          <Col span={8}>
            <BaseForm.Item name="role" label="Role" rules={[{ required: true, message: 'Role required' }]}>
              <Select options={roleSelectData} placeholder="Select role" />
            </BaseForm.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <BaseForm.Item>
              <ButtonWrapper>
                <Button type="primary" loading={isLoading} htmlType="submit">
                  Add New User
                </Button>
              </ButtonWrapper>
            </BaseForm.Item>
          </Col>
        </Row>
      </Card>
    </BaseForm>
  );
};
