import React from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';

// no lazy loading for auth pages to avoid flickering
const AuthLayout = React.lazy(() => import('@app/components/layouts/AuthLayout/AuthLayout'));
import LoginPage from '@app/pages/LoginPage';

import MainLayout from '@app/components/layouts/main/MainLayout/MainLayout';
import ProfileLayout from '@app/components/profile/ProfileLayout';
import RequireAuth from '@app/components/router/RequireAuth';
import { withLoading } from '@app/hocs/withLoading.hoc';
import PriceBySeatPage from '@app/pages/SeatPricebySeatPage';
import PriceByDatePage from '@app/pages/SeatPricebyDatePage';
import AirportListPage from '@app/pages/AirportPage/AirportListPage';
import { ScheduleAddPage } from '@app/pages/SchedulePage/ScheduleAddPage';
import BookPage from '@app/pages/BookPage/BookPage';
import SchedulePage from '@app/pages/SchedulePage/SchedulePage';
import BooklistPage from '@app/pages/BookPage/BooklistPage';
import ApprovelistPage from '@app/pages/BookPage/ApprovelistPage';
import RequireAdminRole from './RequireAdminRole';
import UserPage from '@app/pages/UserPage/UserPage';
import { UserAddPage } from '@app/pages/UserPage/UserAddPage';

const Logout = React.lazy(() => import('./Logout'));

export const DASHBOARD_PATH = '/';

const PersonalInfoPage = React.lazy(() => import('@app/pages/PersonalInfoPage'));
const SecuritySettingsPage = React.lazy(() => import('@app/pages/SecuritySettingsPage'));

// Profile
const PersonalInfo = withLoading(PersonalInfoPage);
const SecuritySettings = withLoading(SecuritySettingsPage);

const AuthLayoutFallback = withLoading(AuthLayout);
const LogoutFallback = withLoading(Logout);

export const AppRouter: React.FC = () => {
  const protectedLayout = (
    <RequireAuth>
      <MainLayout />
    </RequireAuth>
  );

  const adminLayout = (
    <RequireAuth>
      <RequireAdminRole>
        <MainLayout />
      </RequireAdminRole>
    </RequireAuth>
  );

  return (
    <BrowserRouter>
      <Routes>
        <Route path={DASHBOARD_PATH} element={protectedLayout}>
          <Route path="setting/profile" element={<ProfileLayout />}>
            <Route path="personal-info" element={<PersonalInfo />} />
            <Route path="security-settings" element={<SecuritySettings />} />
          </Route>
          <Route path="book_manage/book" element={<BookPage />} />
          <Route path="book_manage/booklist" element={<BooklistPage />} />
          <Route path="book_manage/approvelist" element={<ApprovelistPage />} />
        </Route>
        <Route path={DASHBOARD_PATH} element={adminLayout}>
          <Route path="setting/price_by_seat" element={<PriceBySeatPage />} />
          <Route path="setting/price_by_date" element={<PriceByDatePage />} />
          <Route path="schedule_manage/airport" element={<AirportListPage />} />
          <Route path="schedule_manage/schedule" element={<SchedulePage />} />
          <Route path="schedule_manage/schedule/add" element={<ScheduleAddPage />} />
          <Route path="user_manage/user/add" element={<UserAddPage />} />
          <Route path="user_manage/user" element={<UserPage />} />
        </Route>
        <Route path="/auth" element={<AuthLayoutFallback />}>
          <Route path="login" element={<LoginPage />} />
        </Route>
        <Route path="/logout" element={<LogoutFallback />} />
      </Routes>
    </BrowserRouter>
  );
};
