import React, { useState, useCallback, useEffect } from 'react';
import { PageTitle } from '@app/components/common/PageTitle/PageTitle';
import styled from 'styled-components';
import { Card as CommonCard } from '@app/components/common/Card/Card';
import { useMounted } from '@app/hooks/useMounted';
import { ColumnsType } from 'antd/es/table';
import { TablePaginationConfig } from 'antd';
import { Table } from '@app/components/common/Table/Table';
import { getPriceRulesByDates, updatePriceByDateStatus } from '@app/api/api';
import { Switch } from '@app/components/common/Switch/Switch';

const TablesWrapper = styled.div`
  margin-top: 1.875rem;
`;

const Card = styled(CommonCard)`
  margin-bottom: 2rem;
`;

interface Pagination {
  current?: number;
  pageSize?: number;
  total?: number;
}

interface TableRow {
  id: number;
  greater_than: number;
  less_equal_than: number;
  status: string;
  percentage: number;
}

const PriceByDatePage: React.FC = () => {
  return (
    <>
      <PageTitle>Price by Left Dates</PageTitle>
      <TablesWrapper>
        <Card id="price-by-date-table" title="Price By Date" padding="1.25rem 1.25rem 0">
          <PriceByDateTable />
        </Card>
      </TablesWrapper>
    </>
  );
};

const initialPagination: Pagination = {
  current: 1,
  pageSize: 5,
};

export const PriceByDateTable: React.FC = () => {
  const [tableData, setTableData] = useState<{ data: TableRow[]; pagination: Pagination; loading: boolean }>({
    data: [],
    pagination: initialPagination,
    loading: false,
  });
  const { isMounted } = useMounted();

  const fetch = useCallback(
    (pagination: Pagination) => {
      setTableData((tableData) => ({ ...tableData, loading: true }));
      getPriceRulesByDates(pagination).then((res) => {
        console.log('getPriceRulesByDates', res);
        if (isMounted.current) {
          setTableData({ data: res.price_rules_by_date, pagination: res.pagination, loading: false });
        }
      });
    },
    [isMounted],
  );

  useEffect(() => {
    fetch(initialPagination);
  }, [fetch]);

  const handleTableChange = (pagination: TablePaginationConfig) => {
    fetch(pagination);
  };

  const changeStatus = async (id: number, value: boolean) => {
    await updatePriceByDateStatus({ id, value }).then((res) => {
      if (res.isSuccess) fetch(tableData.pagination);
    });
  };

  const columns: ColumnsType<TableRow> = [
    {
      title: 'Greater Than (>)',
      dataIndex: 'greater_than',
    },
    {
      title: 'Less or Equal Than (<=)',
      dataIndex: 'less_equal_than',
    },
    {
      title: 'Increase By Percent (%)',
      dataIndex: 'percentage',
    },
    {
      title: 'Status',
      dataIndex: 'status',
      key: 'id',
      render: (status: string, record: { id: number }) => {
        return (
          <Switch
            checked={status === 'ENABLE' ? true : false}
            onChange={(checked: boolean) => changeStatus(record.id, checked)}
          />
        );
      },
    },
  ];

  return (
    <Table
      columns={columns}
      dataSource={tableData.data}
      pagination={tableData.pagination}
      loading={tableData.loading}
      onChange={handleTableChange}
      scroll={{ x: 800 }}
      bordered
    />
  );
};

export default PriceByDatePage;
