import React, { useEffect, useState } from 'react';
import { Col, Row } from 'antd';
import { PageTitle } from '@app/components/common/PageTitle/PageTitle';
import { Card } from '@app/components/common/Card/Card';
import { Seatmap } from './Seatmap';
import { BookForm } from './BookForm';
import { useAppSelector } from '@app/hooks/reduxHooks';
import { getBookedSeats } from '@app/api/api';

const BookPage: React.FC = () => {
  const [currentScheduleId, setCurrentScheduleId] = useState<number | undefined>(undefined);
  const [currentSeatId, setCurrentSeatId] = useState<number | undefined>(undefined);
  const [entireSeats, setEntireSeats] = useState<string[]>([]);
  const [selectedSeats, setSelectedSeats] = useState<string[]>([]);
  const [reservedSeats, setReservedSeats] = useState<string[]>([]);

  const aircrafts = useAppSelector((state) => state.baseData.aircrafts);
  const schedules = useAppSelector((state) => state.baseData.schedules);

  useEffect(() => {
    if (!currentScheduleId || !currentSeatId) {
      setEntireSeats([]);
      setSelectedSeats([]);
      setReservedSeats([]);
      return;
    }

    const schedule = schedules.find((schedule) => schedule.id === currentScheduleId);
    if (schedule) {
      const aircraft_id = schedule.aircraft_id;
      const aircraft = aircrafts.find((aircraft) => aircraft.id === aircraft_id);
      if (aircraft) {
        const seatmap = aircraft.seats.find((seat) => seat.id === currentSeatId)!.seatmap;
        setEntireSeats(seatmap.split(','));
      }
    }

    const asyncFunc = async (schedule_id: number, seat_id: number) => {
      await getBookedSeats({ schedule_id: schedule_id, seat_id: seat_id }).then((res: any[]) => {
        const reservedSeats = res.map((item) => {
          return item.seat_number;
        });
        setReservedSeats(reservedSeats);
        setSelectedSeats([]);
      });
    };

    asyncFunc(currentScheduleId, currentSeatId);
  }, [currentScheduleId, currentSeatId]);

  return (
    <>
      <PageTitle>New Book</PageTitle>
      <Row gutter={[30, 30]}>
        <Col xs={24} sm={24} xl={10}>
          <Card id="validation form" title="Seat Map" padding="1.25rem">
            <Seatmap
              entireSeats={entireSeats}
              reservedSeats={reservedSeats}
              selectedSeats={selectedSeats}
              onAddSeat={(seat_number) => {
                setSelectedSeats([...selectedSeats, seat_number]);
              }}
              onRemoveSeat={(seat_number) => {
                const index = selectedSeats.indexOf(seat_number);
                if (index > -1) {
                  selectedSeats.splice(index, 1);
                }
                setSelectedSeats([...selectedSeats]);
              }}
            />
          </Card>
        </Col>

        <Col xs={24} sm={24} xl={14}>
          <Row gutter={[30, 30]}>
            <Col span={24}>
              <BookForm
                onChangeSchedule={(schedule_id, seat_id) => {
                  setCurrentScheduleId(schedule_id);
                  setCurrentSeatId(seat_id);
                }}
                scheduleSeatId={`${currentScheduleId}-${currentSeatId}`}
                selectedSeats={selectedSeats}
              />
            </Col>
          </Row>
        </Col>
      </Row>
    </>
  );
};

export default BookPage;
