import React, { useState, useCallback, useEffect } from 'react';
import { PageTitle } from '@app/components/common/PageTitle/PageTitle';
import styled from 'styled-components';
import { Card as CommonCard } from '@app/components/common/Card/Card';
import { useMounted } from '@app/hooks/useMounted';
import { ColumnsType } from 'antd/es/table';
import { TablePaginationConfig } from 'antd';
import { Table } from '@app/components/common/Table/Table';
import { getUsersForTable } from '@app/api/api';
import { toYYYYMMDD } from '@app/utils/utils';
import { Button } from '@app/components/common/buttons/Button/Button';
import { useNavigate } from 'react-router-dom';

const TablesWrapper = styled.div`
  margin-top: 1.875rem;
`;

const Card = styled(CommonCard)`
  margin-bottom: 2rem;
`;

interface Pagination {
  current?: number;
  pageSize?: number;
  total?: number;
}

interface TableRow {
  id: number;
  name: string;
  birthday: string;
  email: string;
  phone_number: string;
  role: string;
}

const ButtonWrapper = styled.div`
  margin-bottom: 1.25rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: end;
`;

const UserPage: React.FC = () => {
  const navigate = useNavigate();

  return (
    <>
      <PageTitle>Users</PageTitle>
      <TablesWrapper>
        <Card id="user-table" title="Users" padding="1.25rem 1.25rem 0">
          <ButtonWrapper>
            <Button severity="info" onClick={() => navigate('/user_manage/user/add')} loading={false}>
              Add New User
            </Button>
          </ButtonWrapper>
          <UserTable />
        </Card>
      </TablesWrapper>
    </>
  );
};

const initialPagination: Pagination = {
  current: 1,
  pageSize: 5,
};

export const UserTable: React.FC = () => {
  const [tableData, setTableData] = useState<{ data: TableRow[]; pagination: Pagination; loading: boolean }>({
    data: [],
    pagination: initialPagination,
    loading: false,
  });
  const { isMounted } = useMounted();

  const fetch = useCallback(
    (pagination: Pagination) => {
      setTableData((tableData) => ({ ...tableData, loading: true }));
      getUsersForTable(pagination).then((res) => {
        console.log('getUsersForTable', res);
        if (isMounted.current) {
          const users = res.users;
          const data = users.map((user: any) => {
            return {
              id: user.id,
              name: `${user.first_name} ${user.last_name}`,
              birthday: toYYYYMMDD(user.birthday),
              email: user.email,
              phone_number: user.phone_number,
              role: user.role,
            };
          });

          setTableData({ data: data, pagination: res.pagination, loading: false });
        }
      });
    },
    [isMounted],
  );

  useEffect(() => {
    fetch(initialPagination);
  }, [fetch]);

  const handleTableChange = (pagination: TablePaginationConfig) => {
    fetch(pagination);
  };

  const columns: ColumnsType<TableRow> = [
    {
      title: 'Name',
      dataIndex: 'name',
    },
    {
      title: 'Birthday',
      dataIndex: 'birthday',
    },
    {
      title: 'Email',
      dataIndex: 'email',
    },
    {
      title: 'Phone',
      dataIndex: 'phone_number',
    },
    {
      title: 'Role',
      dataIndex: 'role',
    },
  ];

  return (
    <Table
      columns={columns}
      dataSource={tableData.data}
      pagination={tableData.pagination}
      loading={tableData.loading}
      onChange={handleTableChange}
      scroll={{ x: 800 }}
      bordered
    />
  );
};

export default UserPage;
