import { addApprovelist, addBook, createCustomer, getCurrentSeatPrice, invokePayment } from '@app/api/api';
import { Button } from '@app/components/common/buttons/Button/Button';
import { Card as CommonCard } from '@app/components/common/Card/Card';
import { BaseForm } from '@app/components/common/forms/BaseForm/BaseForm';
import { Input } from '@app/components/common/inputs/Input/Input';
import { DatePicker } from '@app/components/common/pickers/DatePicker';
import { Select, Option } from '@app/components/common/selects/Select/Select';
import { ScheduleModel } from '@app/domain/ScheduleModel';
import { useAppSelector } from '@app/hooks/reduxHooks';
import { FONT_SIZE, FONT_WEIGHT } from '@app/styles/themes/constants';
import { mergeBy, toYYYYMMDD_HHMMSS } from '@app/utils/utils';
import { Col, Row } from 'antd';
import { FormInstance } from 'rc-field-form';
import React, { useEffect, useRef, useState } from 'react';
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';
import { notificationController } from '@app/controllers/notificationController';
import socketIOClient from 'socket.io-client';
import moment from 'moment';

const ButtonWrapper = styled.div`
  margin-top: 1.25rem;
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const ScheduleItemWrapper = styled.div`
  padding: 1.25rem;
  border-style: dashed;
  border-width: 1px;
  border-radius: 5px;
`;

const PhoneItem = styled(BaseForm.Item)`
  .ant-input-group-addon {
    padding: 0;
  }

  .ant-input-group-addon > div {
    width: 100% !important;
  }
`;

export const PhoneSelect = styled(Select)`
  width: 5.9375rem;
`;

const Card = styled(CommonCard)`
  width: 100%;
  margin-bottom: 1.25rem;
  .ant-card-body:before {
    display: none;
  }
  .ant-card-body:after {
    display: none;
  }
  &.ant-card-bordered {
    border: 1px solid var(--border-color);
  }
`;

const Details = styled.div`
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
  margin: 1.25rem 0.5rem;
`;

const DetailsTitle = styled.div`
  color: var(--text-light-color);
  font-size: ${FONT_SIZE.md};
  font-weight: ${FONT_WEIGHT.semibold};
  margin-right: 0.5rem;
`;

const DetailsValue = styled.div`
  color: var(--text-main-color);
  font-size: ${FONT_SIZE.lg};
  font-weight: ${FONT_WEIGHT.semibold};
`;

const DetailsContentValue = styled.div`
  color: var(--text-main-color);
  font-size: ${FONT_SIZE.md};
  font-weight: ${FONT_WEIGHT.semibold};
`;

const DetailsRow = styled.div`
  display: flex;
  justify-content: space-between;
`;

interface FieldData {
  name: string | number;
  value?: any;
}

interface BookFormProps {
  onChangeSchedule: (schedule_id: number | undefined, seat_id: number | undefined) => void;
  scheduleSeatId: string;
  selectedSeats: string[];
}

export const BookForm: React.FC<BookFormProps> = ({ onChangeSchedule, scheduleSeatId, selectedSeats }) => {
  const [form] = BaseForm.useForm();
  const [fields, setFields] = useState<FieldData[]>([]);
  const [tripStyle, setTripStyle] = useState<flightType>(undefined);
  const [airportFrom, setAirportFrom] = useState(undefined);
  const [airportTo, setAirportTo] = useState(undefined);
  const [outgoSchedules, setOutgoSchedules] = useState<ScheduleModel[]>([]);
  const [returnSchedules, setReturnSchedules] = useState<ScheduleModel[]>([]);
  const [outgoScheduleSeatId, setOutgoScheduleSeatId] = useState<string>('');
  const [returnScheduleSeatId, setReturnScheduleSeatId] = useState<string>('');
  const [outgoSelectedSeats, setOutgoSelectedSeats] = useState<string[]>([]);
  const [returnSelectedSeats, setReturnSelectedSeats] = useState<string[]>([]);
  const [flightOutStr, setOutFlightStr] = useState('');
  const [flightReturnStr, setReturnFlightStr] = useState('');
  const [outgoSeatPrice, setOutgoSeatPrice] = useState(0);
  const [returnSeatPrice, setReturnSeatPrice] = useState(0);
  const [isLoading, setLoading] = useState(false);
  const currentPaymentRef = useRef('');
  const [isChild, setChildCustomer] = useState(false);
  const [totalCost, setTotalCost] = useState(0);
  const [refAfterPayment, setRefAfterPayment] = useState('');

  type flightType = 'round' | 'oneway' | undefined;

  const flightTypeSelectData = [
    {
      label: 'Round Flight',
      value: 'round',
    },
    {
      label: 'One Way',
      value: 'oneway',
    },
  ];

  const paymentData = [
    {
      label: 'Debit/Credit Card',
      value: 'CARD',
    },
    {
      label: 'Mobile Money',
      value: 'MOBILE',
    },
    {
      label: 'Cash',
      value: 'CASH',
    },
  ];

  const book_btn_label = {
    normal: 'Book Now',
    final: 'Complete Booking',
    payment: 'Proceed to Payment',
    cash: 'Request for approvement',
  };

  const [currentBookBtnLabel, setCurrentBookBtnLabel] = useState(book_btn_label.normal);
  const airports = useAppSelector((state) => state.baseData.airports);
  const schedules = useAppSelector((state) => state.baseData.schedules);
  const user_id = useAppSelector((state) => state.user.user?.id);
  const navigate = useNavigate();

  const onSocketEvent = (data: any) => {
    console.log('REQ_PAYMENT_CONFIRMED', data);
    console.log('REQ_PAYMENT_CONFIRMED', data.reference, currentPaymentRef);
    setRefAfterPayment(data.reference);
  };

  useEffect(() => {
    if (refAfterPayment !== currentPaymentRef.current) return;

    setLoading(false);
    setCurrentBookBtnLabel(book_btn_label.final);
  }, [refAfterPayment]);

  useEffect(() => {
    const socket = socketIOClient(process.env.REACT_APP_SERVER_URL!);
    socket.on('REQ_PAYMENT_CONFIRMED', onSocketEvent);

    // CLEAN UP THE EFFECT
    return () => socket.disconnect();
  }, []);

  useEffect(() => {
    if (outgoScheduleSeatId === scheduleSeatId) setOutgoSelectedSeats(selectedSeats);
    // else
    //   setOutgoSelectedSeats([]);

    if (returnScheduleSeatId === scheduleSeatId) setReturnSelectedSeats(selectedSeats);
    // else
    //   setReturnSelectedSeats([]);
  }, [selectedSeats]);

  useEffect(() => {
    let totalCost = outgoSeatPrice * outgoSelectedSeats.length + returnSeatPrice * returnSelectedSeats.length;
    if (isChild) totalCost = Math.round(totalCost * 0.75);

    setTotalCost(totalCost);
  }, [outgoSelectedSeats, returnSelectedSeats, isChild]);

  useEffect(() => {
    fields.map((field) => {
      if (field.name === 'from' && field.value) setAirportFrom(field.value);
      if (field.name === 'to' && field.value) setAirportTo(field.value);
      if (field.name === 'flightType' && field.value) {
        setTripStyle(field.value);
        if (field.value === 'oneway') {
          setReturnScheduleSeatId('');
          setReturnSelectedSeats([]);
        }
      }
      if (field.name === 'birthday' && field.value) {
        const left_years = Math.round(
          (moment().valueOf() - moment(field.value.format('YYYY-MM-DD'), 'YYYY-MM-DD').valueOf()) /
            24 /
            60 /
            60 /
            1000 /
            365,
        );
        if (left_years <= 12) {
          setChildCustomer(true);
        } else {
          setChildCustomer(false);
        }
      }
    });
  }, [fields]);

  useEffect(() => {
    const outgoSchedules = schedules.filter(
      (schedule) => schedule.depart_airport_id === airportFrom && schedule.arrive_airport_id === airportTo,
    );
    setOutgoSchedules(outgoSchedules);

    if (tripStyle === 'round')
      setReturnSchedules(
        schedules.filter(
          (schedule) => schedule.depart_airport_id === airportTo && schedule.arrive_airport_id === airportFrom,
        ),
      );

    if (!!airportFrom && !!airportTo) {
      setOutFlightStr(
        `${airports.find((airport) => airport.id === airportFrom)!.name}-${
          airports.find((airport) => airport.id === airportTo)!.name
        }`,
      );
      setReturnFlightStr(
        `${airports.find((airport) => airport.id === airportTo)!.name}-${
          airports.find((airport) => airport.id === airportFrom)!.name
        }`,
      );
    }
  }, [airportFrom, airportTo]);

  const airportsSelectData = airports.map((airport) => {
    return { label: airport.name, value: airport.id };
  });

  const setSeatPrice = async (schedule_id: number, seat_id: number, type: string) => {
    await getCurrentSeatPrice({ schedule_id: schedule_id, seat_id: seat_id }).then((res) => {
      if (type === 'outgo') setOutgoSeatPrice(res);

      if (type === 'return') setReturnSeatPrice(res);
    });
  };

  const prefixSelector = (
    <BaseForm.Item name="prefix" noStyle>
      <PhoneSelect>
        <Option value="233">+233</Option>
      </PhoneSelect>
    </BaseForm.Item>
  );

  const onFinish = async () => {
    setLoading(true);
    let json: any = {};

    fields.forEach((field) => {
      const key = field.name;
      const value = String(field.name) === 'birthday' && field.value ? field.value.format('YYYY-MM-DD') : field.value;
      json = {
        ...json,
        ...{
          [key]: value,
        },
      };
    });

    const customerData = {
      first_name: json.first_name,
      last_name: json.last_name,
      email: json.email,
      phone_number: json.phone_number,
      birthday: json.birthday,
    };

    let customer: any;
    await createCustomer(customerData).then((res) => (customer = res));
    console.log(customer);

    const outgo_seats = outgoSelectedSeats.map((seat) => {
      return {
        seat_id: json.outgo_seat_type,
        seat_number: seat,
        price: !isChild ? outgoSeatPrice : outgoSeatPrice * 0.75,
      };
    });

    const return_seats = returnSelectedSeats.map((seat) => {
      return {
        seat_id: json.return_seat_type,
        seat_number: seat,
        price: !isChild ? returnSeatPrice : returnSeatPrice * 0.75,
      };
    });

    const approveData = {
      outgo_schedule_id: json.outgo_schedule_id,
      return_schedule_id: json.return_schedule_id,
      customer_id: customer.id,
      outgo_seats: outgo_seats,
      return_seats: return_seats,
      agent_id: user_id,
    };

    if (json.payment_type === 'CASH') {
      await addApprovelist(approveData).then((res) => {
        if (res.isSuccess) {
          notificationController.success({ message: 'Requested Approvement successfully' });
        } else {
          notificationController.error({ message: res.message });
        }
      });

      setLoading(false);
      navigate('/book_manage/approvelist');
    } else {
      if (currentBookBtnLabel === book_btn_label.payment) {
        const invokePaymentData = {
          email: customerData.email,
          amount: totalCost,
        };

        console.log('invokePaymentData', invokePaymentData);

        await invokePayment(invokePaymentData).then((res) => {
          if (res.status) {
            notificationController.success({ message: 'Invoked payment url successfully' });
            currentPaymentRef.current = res.data.reference;
          } else {
            notificationController.error({ message: 'Invoke payment url failed' });
          }
        });
      } else if (currentBookBtnLabel === book_btn_label.final) {
        const bookData = {
          ...approveData,
          payment_type: json.payment_type,
        };

        await addBook(bookData).then((res) => {
          if (res.isSuccess) {
            notificationController.success({ message: 'Booked successfully' });
          } else {
            notificationController.error({ message: res.message });
          }
        });

        setLoading(false);
        navigate('/book_manage/booklist');
      }
    }
  };

  const onChangePayment = (value: any) => {
    if (value === 'CASH') setCurrentBookBtnLabel(book_btn_label.cash);
    else if (value === 'MOBILE' || value === 'CARD') setCurrentBookBtnLabel(book_btn_label.payment);
  };

  return (
    <BaseForm
      name="bookForm"
      form={form}
      onFinish={onFinish}
      onFieldsChange={(_, allFields) => {
        const currentFields = allFields.map((item) => {
          return {
            name: Array.isArray(item.name) ? item.name[0] : '',
            value: item.value,
          };
        });
        const uniqueData = mergeBy(fields, currentFields, 'name');
        setFields(uniqueData);
      }}
    >
      <Card id="schedule-form" title="Please Select Schedule" padding="1.25rem">
        <Row gutter={[30, 30]}>
          <Col span={8}>
            <BaseForm.Item
              name="flightType"
              label="Trip Type"
              rules={[{ required: true, message: 'Trip Type required' }]}
            >
              <Select options={flightTypeSelectData} placeholder="Select your trip type" />
            </BaseForm.Item>
          </Col>
        </Row>
        <Row gutter={[30, 30]}>
          <Col span={12}>
            <BaseForm.Item name="from" label="From" rules={[{ required: true, message: 'Depart Airport required' }]}>
              <Select options={airportsSelectData} placeholder="Select depart airport" />
            </BaseForm.Item>
          </Col>
          <Col span={12}>
            <BaseForm.Item name="to" label="To" rules={[{ required: true, message: 'Destination Airport required' }]}>
              <Select options={airportsSelectData} placeholder="Select destination airport" />
            </BaseForm.Item>
          </Col>
        </Row>
        <Row gutter={[30, 30]}>
          {!!tripStyle && (
            <Col span={tripStyle === 'round' ? 12 : 24}>
              <SheduleItem
                type={'outgo'}
                schedules={outgoSchedules}
                flight={flightOutStr}
                form={form}
                onSelectSeat={(schedule_id, seat_id) => {
                  onChangeSchedule(schedule_id, seat_id);
                  setOutgoScheduleSeatId(`${schedule_id}-${seat_id}`);
                  setSeatPrice(schedule_id, seat_id, 'outgo');
                }}
              />
            </Col>
          )}
          {tripStyle === 'round' && (
            <>
              <Col span={12}>
                <SheduleItem
                  type={'return'}
                  schedules={returnSchedules}
                  flight={flightReturnStr}
                  form={form}
                  onSelectSeat={(schedule_id, seat_id) => {
                    onChangeSchedule(schedule_id, seat_id);
                    setReturnScheduleSeatId(`${schedule_id}-${seat_id}`);
                    setSeatPrice(schedule_id, seat_id, 'return');
                  }}
                />
              </Col>
            </>
          )}
        </Row>
      </Card>
      <Card id="passenger-form" title="Passenger Information" padding="1.25rem">
        <Row gutter={[30, 30]}>
          <Col span={12}>
            <BaseForm.Item
              name="first_name"
              label="First Name"
              rules={[{ required: true, message: 'First Name required' }]}
            >
              <Input />
            </BaseForm.Item>
          </Col>
          <Col span={12}>
            <BaseForm.Item
              name="last_name"
              label="Last Name"
              rules={[{ required: true, message: 'Last Name required' }]}
            >
              <Input />
            </BaseForm.Item>
          </Col>
        </Row>
        <Row gutter={[30, 30]}>
          <Col span={24}>
            <BaseForm.Item name="birthday" label="Birthday" rules={[{ required: true, message: 'Birthday required' }]}>
              <DatePicker format="YYYY-MM-DD" />
            </BaseForm.Item>
          </Col>
        </Row>
        <Row gutter={[30, 30]}>
          <Col span={12}>
            <PhoneItem name="phone_number" label="Phone" rules={[{ required: true, message: 'Phone number required' }]}>
              <Input addonBefore={prefixSelector} />
            </PhoneItem>
          </Col>
          <Col span={12}>
            <BaseForm.Item
              name="email"
              label="Email"
              rules={[
                {
                  type: 'email',
                  required: true,
                  message: 'Email required',
                },
              ]}
            >
              <Input />
            </BaseForm.Item>
          </Col>
        </Row>
      </Card>
      <Card id="payment-form" title="Payment" padding="1.25rem">
        <Details>
          <DetailsRow>
            <DetailsValue>Total Price:</DetailsValue>
            <DetailsValue>
              {isChild && (
                <>
                  {totalCost}&nbsp;<del>{Math.round(totalCost / 0.75)}</del>&nbsp;$
                </>
              )}
              {!isChild && <>{totalCost}&nbsp;$</>}
            </DetailsValue>
          </DetailsRow>
          {outgoScheduleSeatId !== '' && (
            <>
              <DetailsRow>
                <DetailsValue>Outgoing Flight:</DetailsValue>
              </DetailsRow>
              {outgoSelectedSeats.map((seat) => (
                <DetailsRow key={`outgo-${seat}`}>
                  <DetailsTitle>{seat}:</DetailsTitle>
                  <DetailsContentValue>{outgoSeatPrice}&nbsp;$</DetailsContentValue>
                </DetailsRow>
              ))}
            </>
          )}
          {returnScheduleSeatId !== '' && (
            <>
              <DetailsRow>
                <DetailsValue>Return Flight:</DetailsValue>
              </DetailsRow>
              {returnSelectedSeats.map((seat) => (
                <DetailsRow key={`return-${seat}`}>
                  <DetailsTitle>{seat}:</DetailsTitle>
                  <DetailsContentValue>{returnSeatPrice}&nbsp;$</DetailsContentValue>
                </DetailsRow>
              ))}
            </>
          )}
        </Details>
        <Row gutter={[30, 30]}>
          <Col span={24}>
            <BaseForm.Item
              name="payment_type"
              label="Payment Method"
              rules={[{ required: true, message: 'Payment method required' }]}
            >
              <Select options={paymentData} placeholder="Select your payment method" onChange={onChangePayment} />
            </BaseForm.Item>
          </Col>
          <Col span={24}>
            <BaseForm.Item>
              <ButtonWrapper>
                <Button type="primary" loading={isLoading} htmlType="submit">
                  {currentBookBtnLabel}
                </Button>
              </ButtonWrapper>
            </BaseForm.Item>
          </Col>
        </Row>
      </Card>
    </BaseForm>
  );
};

interface SheduleItemProps {
  type: 'outgo' | 'return';
  schedules: ScheduleModel[];
  flight: string;
  form: FormInstance<any>;
  onSelectSeat: (schedule_id: number, seat_id: number) => void;
}

const SheduleItem: React.FC<SheduleItemProps> = ({ type, schedules, flight, form, onSelectSeat }) => {
  const [seatTypeData, setSeatTypeData] = useState<{ label: string; value: number }[]>([]);
  const [scheduleData, setScheduleData] = useState<{ label: string; value: number }[]>([]);
  const [currentScheduleId, setCurrentScheduleId] = useState<number | undefined>(undefined);
  const [currentSeatId, setCurrentSeatId] = useState<number | undefined>(undefined);

  const aircrafts = useAppSelector((state) => state.baseData.aircrafts);

  useEffect(() => {
    if (schedules.length === 0 || !schedules) return;

    const schedule = schedules.find((schedule) => schedule.id === currentScheduleId);
    if (schedule) {
      const aircraft_id = schedule.aircraft_id;
      const aircraft = aircrafts.find((aircraft) => aircraft.id === aircraft_id);
      if (aircraft) {
        const seats = aircraft.seats;
        setSeatTypeData(
          seats.map((seat) => {
            return { label: seat.seat_type, value: seat.id };
          }),
        );
      }
    }

    form.setFieldValue(`${type}_seat_type`, undefined);
  }, [currentScheduleId]);

  useEffect(() => {
    setSeatTypeData([]);

    const scheduleData = schedules.map((schedule) => {
      return { label: toYYYYMMDD_HHMMSS(schedule.depart_time), value: schedule.id };
    });
    setScheduleData(scheduleData);

    form.setFieldValue(`${type}_schedule_id`, undefined);
    form.setFieldValue(`${type}_seat_type`, undefined);
    onSelectSeat();
  }, [schedules, type]);

  const selectSeat = () => {
    form
      .validateFields([`${type}_schedule_id`, `${type}_seat_type`])
      .then((res: any) => {
        onSelectSeat(currentScheduleId!, currentSeatId!);
      })
      .catch((err: any) => console.log(err));
  };

  return (
    <ScheduleItemWrapper>
      <Details>
        <DetailsRow>
          <DetailsTitle>{`${type.toUpperCase()}:`}</DetailsTitle>
          <DetailsValue>{flight}</DetailsValue>
        </DetailsRow>
      </Details>
      <BaseForm.Item
        name={`${type}_schedule_id`}
        label="Schedule"
        rules={[{ required: true, message: 'Schedule select required' }]}
      >
        <Select options={scheduleData} placeholder="Select" onChange={(value) => setCurrentScheduleId(Number(value))} />
      </BaseForm.Item>
      <BaseForm.Item
        name={`${type}_seat_type`}
        label="Seat Type"
        rules={[{ required: true, message: 'Seat type required' }]}
      >
        <Select options={seatTypeData} placeholder="Select" onChange={(value) => setCurrentSeatId(Number(value))} />
      </BaseForm.Item>
      <ButtonWrapper>
        <Button severity="info" onClick={selectSeat} loading={false}>
          Select Seat
        </Button>
      </ButtonWrapper>
    </ScheduleItemWrapper>
  );
};
